<template>
  <div>
    <BreadCrumb :items="breadCrumbItems"></BreadCrumb>
    <el-card class="box-card">
      <div class="btn">
        <el-button plain @click="createCate"></el-button>
      </div>
      <List
        :data="cates"
        :attrs="ListAttrs"
        @edit_item="editCate"
        @delete_item="deleteCate"
        @view_item="viewCate"
      ></List>
      <pagination
        class="pagination"
        :total="total"
        :pageSizes="[10,50,100]"
        @size_change="handleSizeChange"
        @page_change="handleCurrentPageChange"
      ></pagination>
    </el-card>
  </div>
</template>

<script>
import List from "../common/list";
import BreadCrumb from "../common/breadCrumb";
import Pagination from "../common/pagination";
import { api } from "../../core/api.js";
import { MessageBox } from "element-ui";

export default {
  name: "CategoryList",
  data: function() {
    return {
      breadCrumbItems: [
        { path: "/admin/overview", name: "" },
        { path: "", name: "" }
      ],
      cates: [],
      ListAttrs: [
        { attr: "name", label: "" },
        { attr: "", label: "", type: "operations" }
      ],
      currentPage: 1,
      size: 10,
      total: 0
    };
  },
  components: {
    List,
    BreadCrumb,
    Pagination
  },
  methods: {
    async getCates() {
      let ret = await api.get("/v1/post/categories", {
        params: { page: this.currentPage, size: this.size }
      });
      this.cates = ret.data;
      this.total = ret.page_info.total;
    },
    handleSizeChange(size) {
      this.size = size;
      this.getTags();
    },
    handleCurrentPageChange(page) {
      this.currentPage = page;
      this.getTags();
    },
    createCate() {
      MessageBox.prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(async ({ value }) => {
          let ret = await api.post("/v1/post/categories", { name: value });
          if (!ret) {
            return;
          }
          this.$message({
            type: "success",
            message: '"' + value + '"'
          });
          this.getCates();
        })
        .catch(() => {});
    },
    viewCate(id){
    },
    editCate(id) {
    },
    deleteCate(id) {
    },
  },
  created() {
    this.getCates();
  }
};
</script>

